import React from 'react'
import styled from "styled-components";


const Container=styled.div`
width:100vw;
height:100vh;
background:linear-gradient(rgba(255,255,255,0.7),rgba(255,255,255,0.7)) ,url("https://images.unsplash.com/photo-1509631179647-0177331693ae?ixid=MnwxMjA3fDB8MHxzZWFyY2h8NHx8ZmFzaGlvbnxlbnwwfHwwfHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=700&q=60");
display:flex;
align-items:center;
justify-content:center;
background-size:cover;
`

const Wrapper=styled.div`
width:40%;
padding:20px;
background-color:white;

`

const Title=styled.h1`
font-size:24px;
font-weight:300;
`

const Form=styled.form`
display:flex;
flex-wrap:wrap;
`

const Input=styled.input`
flex:1;
min-width:40%;
margin:20px 10px 0px 0px;
padding:10px;
`

const Agreement=styled.span`
font-size:12px;
margin:20px 0px;

`

const Button=styled.button`
width:40%;
border:none;
padding:15px 20px;
background-color:teal;
color:white;
cursor:pointer;
`


const Register = () => {
    return (
        <Container>
           
            <Wrapper>
                <Title>
            Create an Account
                </Title>
                <Form>
                    <Input placeholder="Name"></Input>
                    <Input placeholder="Last Name"></Input>
                    <Input placeholder="Username"></Input>
                    <Input placeholder="Email"></Input>
                    <Input placeholder="Password"></Input>
                    <Input placeholder="Confirm Password"></Input>
                    <Agreement>
                        By creating an account, I consent to the proccessing of my personal data in accordance with the <b>  PRIVACY POLICY</b>
                    </Agreement>
                    <Button>Create</Button>
                </Form>
            </Wrapper>
         
        </Container>
    )
}

export default Register
