import React from 'react'
import styled from "styled-components";
import {Facebook, Twitter,Instagram, Pinterest,Room,MailOutline,Phone} from "@material-ui/icons";

const Container=styled.div`
display:flex;

`

const Left=styled.div`
flex:1;
display:flex;
flex-direction:column;
padding:20px;
`

const Logo=styled.h1``

const Desp=styled.p`
margin:20px 0px;`

const SocialConatiner=styled.div`
display:flex;`

const SocialIcon=styled.div`
width:40px;
height:40px;
border-radius:50%;
color:white;
background-color:#${props=>props.color};
display:flex;
align-items:center;
justify-content:center;
margin-right:20px;
`


const Center=styled.div`
flex:1;
padding:20px;
`
const Title=styled.h3`
margin-bottom:30px;
`
const List=styled.ul`
margin:0;
padding:0;
list-style:none;
flex-wrap:wrap;
display:flex;
`
const ListItem=styled.li`
width:50%;
margin-bottom:10px;

`

const Right=styled.div`
flex:1;
padding:20px;

`
const ContactItem=styled.div`
margin-bottom:20px;
display:flex;
align-items:center;

`

const Payment=styled.img`
width:50%;

`




const Footer = () => {
    return (
        <Container>
        <Left>
            <Logo>
                MyShop
            </Logo>
            <Desp>
                This is the place you were looking for your whole life and it's here. So, what are you waiting for?
            </Desp>
            <SocialConatiner>
                <SocialIcon color="3B5999">
                    <Facebook/>
                </SocialIcon>
                <SocialIcon color="E4405F">
                    <Twitter/>
                </SocialIcon>
                <SocialIcon color="55ACEE">
                    <Instagram/>
                </SocialIcon>
                <SocialIcon color="E60023"><Pinterest/></SocialIcon>
            </SocialConatiner>
        </Left>
        <Center>
            <Title>Useful Links</Title>
            <List>
                <ListItem>Home</ListItem>
                <ListItem>Cart</ListItem>
                <ListItem>Man Fashion</ListItem>
                <ListItem>Woman Fashion</ListItem>
                <ListItem>Accessories</ListItem>
                <ListItem>My Account</ListItem>
                <ListItem>Order Tracking</ListItem>
                <ListItem>Wishlist</ListItem>
                <ListItem>Terms</ListItem>
               
            </List>
            
          
            
        </Center>
        
        <Right>
            <Title>Contact</Title>
            <ContactItem>
               <Room style={{marginRight:"10px"}}/> xyz, Agra, India
            </ContactItem>
            <ContactItem><Phone style={{marginRight:"10px"}}/>+912345678900 </ContactItem>
            <ContactItem><MailOutline style={{marginRight:"10px"}}/>myShop@contact.com</ContactItem>
            <Payment src="https://i.ibb.co/Qfvn4z6/payment.png"/>

        </Right>
            
        </Container>
    )
}

export default Footer
